.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #0E0D13;
  border-radius: 32px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  padding: 24px 0;
  z-index: 1000;
}
@media screen and (min-width: 768px) {
  .footer {
    border-bottom-left-radius: 32px;
    border-bottom-right-radius: 32px;
  }
}